.mainarea{
    margin:auto;
    width: 50%;
    height: 240px;
    min-width: 300px;
    display: grid;
    align-items: center;
    grid-template-rows: 30% auto;
}
.first{
    width: 100%;
    margin: auto;
    display: grid;
    grid-template-columns: 80% auto;
    gap: 0 5px;
}
input{
    font-size: 20px;
    padding: 5px;
    border-radius: 5px;
}
button{
    background-color: rgb(26, 177, 177);
    padding: 5px;
    border-radius: 5px;
    width: 80px;
}
p{
    font-size: 20px;
}
#message{
    position: absolute;
    top:0;
    text-align: center;
    width: 240px;
    left: calc(50% - 120px); 
    background-color: antiquewhite;
    color: green;
    font-size: 18px;
    margin: 0;
    padding: 15px;
}
span{
    color: limegreen;
}
.shorten{
    width: auto;
}  
img{
    width: 300px;
    height: 300px;
    position: absolute;
    left: calc((50% - 150px));
    top: calc(50% - 150px);
} 
